<template>
    <div>

        <page-head
                avator="https://img.yzcdn.cn/public_files/2017/10/24/f6aabd6ac5521195e01e8e89ee9fc63f.jpeg"
                ptitle=""

                pdesc=""

        />
        <van-cell-group style="margin-top: 10px">

            <van-field
                    v-model="name"
                    label=""
                    placeholder=""
                    clearable



            />
            <van-field  v-model="gzh" label="" placeholder="" clearable/>
            <van-field  v-model="sjhm" label="" placeholder="" clearable/>

        </van-cell-group>
        <van-collapse v-model="activeSexkl" style="margin-top: 10px">
            <van-collapse-item title="" name="1">
                <van-radio-group v-model="radioSex">

                    <van-cell title="" clickable @click="radioSex = '1'">
                        <van-radio slot="right-icon" name="1" />
                    </van-cell>
                    <van-cell title="" clickable @click="radioSex = '2'">
                        <van-radio slot="right-icon" checked-color="red" name="2" />
                    </van-cell>

                </van-radio-group>
            </van-collapse-item>


        </van-collapse>
        <van-field
                style="margin-top: 10px"
                readonly
                clickable
                :value="dzzw"
                label=""
                placeholder=""
                @click="showPickerdzzw = true"
                rightIcon="arrow"/>


        <van-popup v-model="showPickerdzzw" position="bottom">
            <van-picker
                    show-toolbar
                    :columns="columnsdzzw"
                    @cancel="showPickerdzzw = false"
                    @confirm="onConfirmdzzw"
            />
        </van-popup>

        <van-field

                readonly
                clickable
                :value="jszc"
                label=""
                placeholder=""
                @click="showPickerjszc = true"
                rightIcon="arrow"/>


        <van-popup v-model="showPickerjszc" position="bottom">
            <van-picker
                    show-toolbar
                    :columns="columnsjszc"
                    @cancel="showPickerjszc = false"
                    @confirm="onConfirmjszc"
            />
        </van-popup>
        <van-field

                readonly
                clickable
                :value="szdw"
                label=""
                placeholder=""
                @click="showPickerszdw = true"
                rightIcon="arrow"/>


        <van-popup v-model="showPickerszdw" position="bottom">
            <van-picker
                    show-toolbar
                    :columns="columnsszdw"
                    @cancel="showPickerszdw = false"
                    @confirm="onConfirmszdw"
            />
        </van-popup>


        <van-button style="margin:20px auto;width: 90%" type="primary" block></van-button>
    </div>
</template>
<script>


    import PageHead from "../components/pagehead";
    export default {
        components: {PageHead},
        data() {
            return {
                name: '',

                radioSex: '1',

                activeSexkl: [''],
                gzh: '',

                sjhm: '',

                showPickerdzzw: false,
                showPickerjszc: false,
                showPickerszdw: false,
                dzzw:'',
                jszc:'',
                szdw:'',
                columnsdzzw: ['', '', '', '', ''],
                columnsjszc: ['', '', '', '', ''],
                columnsszdw: ['', '', '', '', '']



            }


        },
        methods:{

            onConfirmdzzw(value) {
                this.dzzw = value;
                this.showPickerdzzw = false;
            },
            onConfirmjszc(value) {
                this.jszc = value;
                this.showPickerjszc = false;
            },
            onConfirmszdw(value) {
                this.szdw = value;
                this.showPickerszdw = false;
            }





        },



    }
</script>
<style type="text/css">




</style>