                finishedzxhd: false,
                loadingcjhd: false,
                finishedcjhd: false


            }
        },
        methods: {
            onLoadzxhd() {
                // 
                setTimeout(() => {
                    for (let i = 0; i < 10; i++) {
                        this.list.push(this.list.length + 1);
                    }
                    // 
                    this.loadingzxhd = false;

                    // 
                    if (this.list.length >= 40) {
                        this.finishedzxhd = true;
                    }
                }, 500);
            },
            onLoadcjhd() {
                // 
                setTimeout(() => {
                    for (let i = 0; i < 10; i++) {
                        this.list.push(this.list.length + 1);
                    }
                    // 
                    this.loadingcjhd = false;

