<template>
    <div>

        <PageHead
                avator="https://img.yzcdn.cn/public_files/2017/10/24/320454216bbe9e25c7651e1fa51b31fd.jpeg"
                ptitle=""
                pdesc=""
        />
        <van-cell-group style="margin-top: 10px">

            <van-field
                    v-model="name"
                    label=""
                    placeholder=""
                    clearable


            />
            <van-field v-model="sfzh" label="" placeholder="" clearable/>
            <van-field v-model="sjhm" label="" type="digit" placeholder="" clearable/>
            <van-field v-model="szbj" label="" type="digit" placeholder=" 12 12" clearable/>
        </van-cell-group>
        <van-collapse v-model="activeSexkl" style="margin-top: 10px" accordion>
            <van-collapse-item title="" :value=radioSex :name="collapsename">
                <van-radio-group v-model="radioSex">

                    <van-cell title="" clickable @click="radioSex = '',collapsename='4'">
                        <van-radio slot="right-icon" name=""/>
                    </van-cell>
                    <van-cell title="" clickable @click="radioSex = '',collapsename='3'">
                        <van-radio slot="right-icon" checked-color="red" name=""/>
                    </van-cell>

                </van-radio-group>
            </van-collapse-item>
            <van-collapse-item title="" :value=radioKl :name="colname">
                <van-radio-group v-model="radioKl">

                    <van-cell title="" clickable @click="radioKl = '',colname='5'">
                        <van-radio slot="right-icon" name=""/>
                    </van-cell>
                    <van-cell title="" clickable @click="radioKl = '',colname='6'">
                        <van-radio slot="right-icon" checked-color="red" name=""/>
                    </van-cell>

                </van-radio-group>
            </van-collapse-item>

        </van-collapse>
        <van-field
                style="margin-top: 10px"
                readonly
                clickable
                :value="toareaText"
                label="/"
                placeholder="/"
                @click="showPickerXxss = true"
                rightIcon="arrow"/>


        <van-popup v-model="showPickerXxss" position="bottom">
            <van-area
                    :area-list="areaList"
                    :columns-num="2"
                    @cancel="showPickerXxss = false"
                    @confirm="onAreaConfirm"
                    v-model="areaText"
            />
        </van-popup>

        <van-field
                readonly
                clickable
                label="/"
                placeholder=""
                @click="showXxmc = true"
                v-model="valuezxmc"
                rightIcon="arrow"/>
        <van-overlay :show="showXxmc" @click="showXxmc = false">
            <div @click.stop>
                <van-row type="flex" justify="center">
                    <van-col span="24" style="margin:20px 10px 5px 10px">
                        <van-search placeholder=""/>
                    </van-col>
                </van-row>
                <van-row type="flex" justify="center">
                    <van-col span="24" style="margin:0px 10px 10px 10px">
                        <van-list>
                            <van-cell
                                    value="//"
                                    clickable
                                    @click="showXxmc = false,valuezxmc='//'"
                            />
                            <van-cell
                                    value="//"
                                    clickable
                                    @click="showXxmc = false"
                            />
                            <van-cell
                                    value="//"
                                    clickable
                                    @click="showXxmc = false"
                            />
                        </van-list>

                    </van-col>
                </van-row>
            </div>
        </van-overlay>
        <van-button style="margin:20px auto;width: 90%" type="primary" block></van-button>
    </div>
</template>
<script>
    import AreaList from '../assets/area';
    import PageHead from "../components/pagehead";

    export default {
        components: {PageHead},
        data() {
            return {
                collapsename:'',
                colname:'1',
                valuezxmc:'',
                name:'',
                radioSex: '',
                radioKl: '',
                activeSexkl: [''],
                sfzh: '',
                szbj: '',
                sjhm: '',
                Xxss: '',
                showPickerXxss: false,
                areaList: AreaList,
                areaText: '',

                province:'',
                city:'',
                showXxmc: false


            }


        },
        methods: {

            onAreaConfirm(values) {
                values= values.filter(value => !!value);
                this.province=values[0].name;
                this.city=values[1].name;
                // console.log(this.city);
                this.showPickerXxss = false;

            }


        },
        computed:{
            toareaText() {



                const arr=[this.province,this.city]

                if (this.province && this.province === this.city) {
                    arr.splice(1, 1);
                }
                return arr.filter(text => text).join('/');


            }
        }








    }
</script>
<style type="text/css">
    .page_hd {

        background-color: white;
    }

    .page_title {
        text-align: left;


    }

    .page_desc {

        color: rgba(0, 0, 0, .5);
        text-align: left;
        font-size: 14px
    }

    img.avator {
        margin: 10px;
        height: 60px;
        width: 60px;
        border-radius: 50%
    }

    .page_cont {
        padding: 10px 0;
        margin: 0px 10px 0px 0px
    }


</style>